section.lessonsWrap {
	lost-flex-container: row;
	margin-bottom: 5rem;
	.lessonColumn {
		lost-column: 8/12;
		/*float: left;*/
	}
	ol {
		counter-reset: num;
		list-style-type: none;
		li:before {
			font-size: 2.25rem;
			font-family: $headerFont;
			font-weight: 700;
			counter-increment: num;
			content: counters(num,".") " ";
		}
	}
	ol.lessonColumn {
		margin: 0;
		padding: 0;
		& > li {
			margin-bottom: 5rem;
			&:before {
				content: '';
			}
			&:last-child ul {
				margin: 0;
				padding: 0;
				list-style: none;
				li:before {
					content: '';
				}
			}
		}
		li.new-lessonRow:before {
			content: '';
		}
		li:last-child {
			border-bottom: 0;
		}
	}
}




	ol.lessonColumn .card {
	padding: 10px 20px;
	ol {
		margin: 0;
		padding: 0;
		li {
			padding: 20px 10px;
		}
	}
}

li.lessonRow {
	lost-flex-container: row;
	align-items: center;
	border-bottom: 1px solid $border;
	&:before {
		lost-column: 2/12;
	}
	&:hover > .lessonMeta span {
		display: inline-block;
	}
	a {
		display: inline-block;
		border: 0;
		color: $black;
	}
	.lessonInfo {
		lost-column: 7/12;
	}
	.lessonMeta {
		lost-column: 3/12;
		lost-flex-container: row;
		align-items: center;
		justify-content: flex-end;
		color: $grey;
		span {
			display: none;
			margin-right: 10px;
		}
		a {
			display: inline-block;
			border: none;
			color: $grey;
		}
	}
}



li.new-lessonRow {
	lost-flex-container: row;
	align-items: center;
	/*padding: 25px 10px;*/
	p {
		cursor: pointer;
		font-size: 1.4rem;
		font-family: $baseFont;
	}
}

article.lessonNew {
	background: #fff;
	padding: 30px 20px;
	box-shadow: 0px 3px 7px 0px rgba(0,0,0,0.12);
}

.lessonMeta {
	lost-column: 4/12;
	lost-flex-container: column;
	.sideCard {
		lost-row: 1/3;
		.card {
			padding: 20px;
			button {
				width: 100%;
			}
		}
	}
	button.error {
		margin-right: 0;
	}
}

.supersticky {
	position: fixed;
	top: 0;
	min-width: 307px;
	width: auto;
	margin-bottom: 200px;
}

.topicLegend {
	ul {
		padding: 0 0 20px 10px;
		margin: 0 0 30px;
		list-style: none;
		font-weight: 700;
		font-family: $headerFont;
		border-bottom: 1px solid $border;
		a {
			text-decoration: none;
			border: none;
			color: $black;
		}
		li.active {
			color: $red;
			position: relative;
			&:before {
				content: '>';
				position: absolute;
			  left: -12px;
			}
		}
	}
	button {
		width: 100%;
	}
}
